.footer {
  padding-top: 94px;
  padding-bottom: 64px;
  position: relative;
  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 93%;
    height: 100%;
    z-index: -1;
    background: $color-blue;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
    background: $color-button;
  }



  .footer__content {
    @include container;
    display: flex;
    flex-wrap: wrap;
  }

  a {
    @include link-reset;
    color: $color-white;
    width: 60%;
  }

  .footer__col1 {
    width: 400px;
    color: $color-white;
    letter-spacing: 0.05em;

    .footer__logo {
      display: flex;
      align-items: center;
      margin-bottom: 34px;

      svg {
        padding-top: 7px;
        padding-left: 1px;
      }

      span {
        font-weight: 600;
        font-size: 28px;
        line-height: 46px;
        letter-spacing: 0.02em;
        margin-left: 15px;
      }

      a {
        @include link-reset;
        color: $color-white;

        width: 60%;
      }
    }

    p {
      @include p;

      width: 60%;
    }

    span {
      width: 36px;
      font-weight: 600;
    }

    .footer__item {
      display: flex;
      align-items: baseline;
      margin-bottom: 23px;

      a {
        @include link-reset;
        color: $color-white;
        margin-bottom: 8px;
        width: 60%;
      }
    }
  }

  .footer__col2,
  .footer__col3 {
    width: 220px;
    padding-right: 20px;

    h3 {
      @include h3;
      color: $color-white;
      margin-bottom: 32px;
    }

    ul {
      @include list-reset;
    }

    li {
      margin-bottom: 16px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    a {
      @include link-reset;
      @include p;
      color: $color-white;
      text-transform: capitalize;
    }
  }

  .footer__copyright {
    @include p;
    width: 100%;
    padding-top: 37px;
    color: $color-white;
    opacity: 0.4;
  }
}
